* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

p {
  margin-bottom: 10px;
}

.timer {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.field {
  text-align: center;
  margin: 0 10px;
}

.value {
  font-size: 36px;
  font-weight: bold;
}

.label {
  color: #888;
  font-size: 14px;
}

input[type="text"] {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  font-size: 14px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: bold;
  transition: background-color .3s;
}

button:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

button:hover {
  background-color: #45a049;
}

.form {
  max-width: 400px;
  background-color: #fff;
  border-radius: 4px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.form label {
  margin-bottom: 10px;
  display: block;
}

.form input[type="number"] {
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  font-size: 14px;
}

.form button[type="submit"] {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 10px;
  font-size: 14px;
  font-weight: bold;
  transition: background-color .3s;
  display: block;
}

.form button[type="submit"]:hover {
  background-color: #45a049;
}

